import React,{ Component } from 'react';
import {linkedIn, facebook, github,line,title} from "img";
import { Sidebar, Item } from './header';
import {withRouter} from 'react-router-dom';
class WrapHeader extends Component {
  state = {
    isOpen: false,
    isClose: true
  };

  handleIsOpen() {
    this.setState(state => ({
      isOpen: !state.isOpen
    }));
  }

  navigator(){
      console.log(this.props);
      this.props.history.push('./history')
  }
  render() {
    return (
        <div className="wrap-header">
            <div className="icons">
                <a href="https://www.facebook.com/profile.php?id=100026987103248"><img src={facebook} alt="icon" /></a>
                <a href="https://www.linkedin.com/in/wirawat-posriya-890039146"><img src={linkedIn} alt="icons" /></a>
                <a href="https://line.me/ti/p/ZfAA7rqroR"><img src={line} alt="icons" /></a>
                <a href="https://github.com/mrnakae304"><img src={github} alt="icons" /></a>
            </div>
            <div className="menu-icon" onClick={this.handleIsOpen.bind(this)}>
                <img src={title} alt="title" />
            </div>
            <Sidebar className="global-nav__list" pose={this.state.isOpen ? 'open' : 'closed'}>
                <Item className="closed-navbar" onClick={this.handleIsOpen.bind(this)}>X</Item>
                <Item className="item" onClick={() => this.props.history.push('./')}>HOME</Item>
                <Item className="item" onClick={() => this.navigator()}>WORKS</Item>
                <Item className="item" ><a href="./">ABOUT ME</a></Item>
                <Item className="item" onClick={() => this.props.history.push('./contact')}>CONTACT</Item>
            </Sidebar>
    </div>);
  }
}

export default withRouter(WrapHeader);