import "./header.css";
import posed from 'react-pose';
import WrapHeader from './WrapHeader';

export const Sidebar = posed.ul({
  open: {
    x: '0%',
    delayChildren: 200,
    staggerChildren: 50
  },
  closed: { x: '200%', delay: 300 },
  initialPose: 'closed'
});

export const Item = posed.li({
  open: { y: 0, opacity: 1 },
  closed: { y: 20, opacity: 0 }
});

export default WrapHeader;
