import React, { Component } from 'react';
import './layout.css';
import { TitleName,LeftContent } from "../content/name_title";
import {WrapHeader} from "../content/header";
import icon from "img/iconfinder-icon.svg";
import {Scroll} from "../content/scroll/scroll";
import { Guideline } from "../content/guideline/guideline";
import posed from 'react-pose';
import {Route, Switch } from "react-router-dom";
import {History } from '../working_experience';
import { Contact } from '../contact/contact';

const Box = posed.div({
  visible: {
    x: ({X}) => (`${-X}px`),
    y: ({Y}) => (`${-Y}px`), // Resolved on `visible` enter
    transition: {
      x: { type: 'spring' , ease: 'easeIn', duration: 3000},
      y: { type: 'spring', ease: 'easeIn', duration: 3000} // Resolved on `visible` enter
    }
  },
  hide:{
    x: ({X}) => (`${-X}px`),
    y: ({Y}) => (`${-Y}px`), // Resolved on `visible` enter
    transition: {
      x: { type: 'spring' ,ease: 'easeOut', duration: 3000},
      y: { type: 'spring', ease: 'easeOut', duration: 3000 } // Resolved on `visible` enter
    }
  }
});

class MainContent extends Component{
  state = {
    img: icon,
    wording: {
      scroll: "tap guideline"
    },
    isVisible: true,
    isOpen: true,
    valY: 0,
    valX: 0,
    pose: "visible"
  }

  hoverEvent(e) {
    e.persist();
    this.setState(state => {
      return ({
        valX: e.movementX,
        valY: e.movementY,
        isVisible: !state.isVisible
      });
    });
  }

  componentDidMount() {
    console.log(this.props)
  }

  render(){
     const { isVisible , valX, valY, img, wording} = this.state
     return(


      <div className="content-container" onMouseOver={(e) => this.hoverEvent(e)}>
        <TitleName />
        <Box className="animate-box" pose={isVisible ? "visible" : "hide"} Y={valY / 10}  X={valX / 10} >
        <div>
          <LeftContent mainBG={img} />
        </div>
        </Box>
        <Scroll scroll={wording.scroll}  />
      </div>
    )}

}

class CreateLayout extends Component {
  // state = {
  //   isVisible: true,
  //   isOpen: true,
  //   valY: 0,
  //   valX: 0,
  //   pose: "visible"
  //
  // }

  // handleClick() {
  //   this.setState(state => ({
  //     isVisible: !state.isVisible
  //   }));
  // }

  render() {
    return(
        <div className="main-container" >
          <header className="headers" >
            <a href="./" className="name">Weerawat</a>
            <WrapHeader />
          </header>
          <main className="content" >
              <Switch>
                <Route path="/" exact component={ MainContent}  state={'hi'} />
                <Route path="/history" component={ History} />
                <Route path="/contact" component={ Contact } />
              </Switch>
          </main>
          <Guideline />
        </div>
    )
  }
}

export default CreateLayout;
