import React from 'react';
import './contact.css';
import {linkedIn, facebook, github,line} from "img";

const socials = [
    {social: 'facebook', img: facebook},
    {social: 'line', img: line},
    {social: 'github', img: github},
    {social: 'linkedin', img:linkedIn}
]


export const Contact = () => {
    return(
        <div className="contact-container">
            {socials.map((socials,index) => {
                return(
                    <div key={index} className={`${socials.social} ball`} >
                        <img  alt={socials.social} src={socials.img} key={index} />
                        <p>{socials.social }</p>
                    </div>)
            })}
            {/*<div className={'facebook'}></div>*/}
        </div>
    )
}